import React from "react"
import styled from "styled-components"
import { StaticQuery, graphql } from "gatsby"
import PerfectScrollbar from 'react-perfect-scrollbar'
import { withPrefix } from "gatsby"
import posed from 'react-pose'

import {TweenLite, Power2, Power3} from "gsap/TweenMax";

import { ScrollToPlugin } from "gsap/all";

import { connect } from 'react-redux';
 
//or get to the parts that aren't included inside TweenMax:
import Draggable from "gsap/Draggable";
// import ScrollToPlugin from "gsap/ScrollToPlugin";




import StyledLiOdd from './styledliodd'
import StyledLiEven from './styledlieven'


const scrollPlugin = ScrollToPlugin;

const ScrollWrapper = posed.div({
  hide: { y: -100, opacity: 1 },
  show: { y: 0, opacity: 1, delay: 500 },
  transition: {
    hide: { ease: 'easeInOut', duration: 1000 },
    show: { ease: 'easeInOut', duration: 1000 }
  }
});

const StyledScrollWrapper = styled(ScrollWrapper)`
  position: absolute;
  top: 0;
`


const StyledPerfectScrollbar = styled(PerfectScrollbar)`
  overflow-x: scroll;
  position: relative;
  display: flex;
  width: 100vw;
  // height: 100vh;
  top: 15px;
  z-index: 995;
  
  div:nth-child(1) {
    display: none
  }
`

const mapStateToProps = (state) => {
  return {
      isInfoPage: state.app.isInfoPage,
      isInViewport: state.app.isInViewport
  }
}

class MenuTop extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      menuItems: null,
      isInViewport: null
    };
    
    this.addElements = this.addElements.bind(this);
    this.handleScroll = this.handleScroll.bind(this);
    this.handleMenuScroll = this.handleMenuScroll.bind(this);
    this.handleRedirect = this.handleRedirect.bind(this);

    this.initPageScroll = 0;

  }

  componentWillMount(){

    const data = this.props.data.allPrismicPage.edges;

    this.setState({
        menuItems: data
      });

  }

  addElements(){

    let arrayToDuplicate = this.state.menuItems;
    
    this.setState(prevState => ({
        menuItems: [...prevState.menuItems, ...arrayToDuplicate, ...arrayToDuplicate]
    }));

  }


  handleScroll() {

      this.addElements();

  }

  componentWillReceiveProps(){

      let menuTopNodes = this.menuRef.children;

      let menuTopNodesArray = Array.from(menuTopNodes);

      menuTopNodesArray.forEach((element) => {

        console.log(withPrefix(`${this.props.isInViewport}/`));
        console.log(element.children[0].getAttribute('href'));

        element.children[0].children[0].children[0].style.color = 'blue';
        element.children[0].children[0].style.backgroundColor = 'white';

        let inViewport = `${withPrefix(this.props.isInViewport)}/`

        
        if(inViewport == element.children[0].getAttribute('href') ) {
          console.log(element);
          element.children[0].children[0].children[0].style.color = 'white';
          element.children[0].children[0].style.backgroundColor = 'blue';
        }
      })
  }

  componentDidMount(){

    this.addElements();
    this.addElements();
     
    Draggable.create(this.menuRef, {type:"scroll", throwProps:true});

    // setInterval(() => {
    //   console.log(this.props.isInViewport);

    //   let menuTopNodes = document.querySelector('.scrollbar-container').children;

    //   let menuTopNodesArray = Array.from(menuTopNodes);

    //   menuTopNodesArray.forEach((element) => {
    //     console.log(withPrefix(`${this.props.isInViewport}/`));
    //     console.log(element.children[0].getAttribute('href'));

    //     element.children[0].children[0].children[0].style.color = 'blue';
    //     element.children[0].children[0].style.backgroundColor = 'white';

        
    //     if(`/${withPrefix(this.props.isInViewport)}/` == element.children[0].getAttribute('href') ) {
    //       console.log(element.children[0]);
    //       element.children[0].children[0].children[0].style.color = 'white';
    //       element.children[0].children[0].style.backgroundColor = 'blue';
    //     }
    //   })
    // },2000);
    

    setTimeout(function(){
      TweenLite.to(this.menuRef, 2, {scrollTo:{x: 750}, ease:Power2.easeOut});
    }.bind(this), 1000);


    setInterval(function(){
      document.querySelector('.infinite-scroll').addEventListener('scroll',this.handleMenuScroll);
    }.bind(this),500)

  }

  handleRedirect(){
    console.log('clicked');
    setTimeout(function(){
      document.querySelector('.infinite-scroll').addEventListener('scroll',this.handleMenuScroll);
    }.bind(this),1000);
  }

  handleMenuScroll() {

      let initPageScroll = this.initPageScroll;
      let menuScroll = this.menuRef.scrollLeft;
      let window = document.querySelector('.infinite-scroll');

      // console.log(window.scrollTop);

      if(window.scrollTop >= initPageScroll) {

        // console.log('scrolling down!');
        TweenLite.to(this.menuRef, 1, {scrollTo:{x: menuScroll + 20}, ease:Power3.easeOut});
        menuScroll ++;

      } else if (window.scrollTop <= initPageScroll ) {

        // console.log('scrolling up!')
        TweenLite.to(this.menuRef, 1, {scrollTo:{x: menuScroll - 20}, ease:Power3.easeOut});
        menuScroll--;

      }

      this.initPageScroll = window.scrollTop;
  }

  


  render() {
    const {menuItems} = this.state;

    let counter = 0;


    return (
      <StyledScrollWrapper pose={this.props.isInfoPage ? 'hide' : 'show'}>
            <StyledPerfectScrollbar onXReachEnd={this.handleScroll} containerRef={ div => this.menuRef = div }>
            {menuItems.map((item, index) => {
      
              counter ++;
      
              if (counter % 2 === 0 ) {
                return (
                  <StyledLiEven
                  onClick={this.handleRedirect}
                  key={index} 
                  item={item}
                  />
                )
              } else {
                return (
                  <StyledLiOdd
                  onClick={this.handleRedirect} 
                  key={index} 
                  item={item}/>
                )
              }
            }
            )}
            </StyledPerfectScrollbar>
      </StyledScrollWrapper>
    )
  }
}


// export default MenuTop

const queryMenuTop = props => (
    <StaticQuery
      query={graphql`
        query {
            allPrismicPage {
                edges {
                  node {
                    tags
                    uid
                    data {
                      title {
                        text
                      }
                    }
                  }
                }
              }
        }
      `}
      render={data => <MenuTop data={data} {...props} />}
    />
  )


  export default connect(mapStateToProps)(queryMenuTop);